#banner{
    background: linear-gradient(175deg, #0E6E84 50%, #ffffff 50.4%);
}
#profileCircle{
    margin: auto;
    height: 225px;
    width: 225px;
    background-color: #ffffff;
    border: 5px solid #0E6E84;
}
#profileImg{
    width: 182px;
}
#profileName{
    font-family: 'Open Sans', sans-serif;
    font-weight: bold;
    font-size: 45px;
    color: #0E6E84;
}
